.add-title {
  text-align: center;
  color: #fff;
}

input[type="text"] {
  color: white;
  border: none;
  background: rgba(0, 0, 0, 0.2);
  width: 100%;
  height: 50px;
  padding: 10px;
  box-sizing: border-box;
  font-size: 20px;
  font-family: "Times New Roman", Times, serif;
  border-radius: 5px;
}
input[type="text"]::placeholder {
  color: #c0c0c0;
}
